@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes spin {
  to {
    transform: rotate(360deg); } }

@keyframes slide-up {
  0% {
    transform: translateY(105%); }
  100% {
    transform: translateY(0%); } }

@keyframes fade-in-scale {
  0% {
    opacity: 0;
    transform: scale(1.2); }
  75% {
    opacity: 1; }
  100% {
    transform: scale(1); } }

@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translate3d(0, 30px, 0); }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.fade-in {
